  /*custom checkbox or radiobutton (square dark-green checkbox)--------------*/
  /* Customize the label (the container) */
  .checkBoxContainer {
    display: block;
    position: relative;
    padding-left: 35px;
    margin-bottom: 12px;
    cursor: pointer;
    font-size: 1rem;
    padding-top: 0.3rem;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }
  
  /* Hide the browser's default checkbox */
  .checkBoxContainer input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0.1px;
    width: 0.1px;
    overflow: hidden;
    z-index: -1
  }
  
  /* Create a custom checkbox */
  .checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 25px;
    width: 25px;
    background-color: #f7f7f7;
    border: 2px solid #ced4da;
    border-radius: 4px;
  }
  
  /* On mouse-over, add a grey background color */
  .checkBoxContainer:hover input ~ .checkmark {
    background-color: #ccc;
  }
  
  /* When the checkbox is checked, add a  background color */
  .checkBoxContainer input:checked ~ .checkmark {
    background-color: white;
  }
  
  /* Create the checkmark/indicator (hidden when not checked) */
  .checkmark:after {
    content: "";
    position: absolute;
    display: none;
  }
  
  /* Show the checkmark when checked */
  .checkBoxContainer input:checked ~ .checkmark:after {
    display: block;
  }
  
  /* Style the checkmark/indicator */
  .checkBoxContainer .checkmark:after {
    left: 8px;
    top: 4px;
    width: 5px;
    height: 10px;
    border: solid #F59D18;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
  }
  /*add outline when navigating with keyboard*/
  .checkBoxContainer input:focus ~ .checkmark {
    border-color: #ffe480;
    box-shadow: inset 0 1px 1px rgba(0,0,0, .075) ,0 0 8px rgba(107, 107, 107, 0.6);
  }
  /*end custom checkbox or radiobutton (square dark-green checkbox)--------------*/
  
  /*custom radiobutton - circle (use custom checkbox or radiobutton class '.checkBoxContainer' and this class)---------------------------*/
  .radioCircle .checkmark{
    border-radius: 50%;
  }
  /* When the checkbox is checked, add a  background color */
  .radioCircle input:checked ~ .checkmark {
    background-color: #F59D18;
  }
  .radioCircle .checkmark:after{
    width: 0.1px;
    height: 0.1px;
    border-color: #F59D18;
  }
  /*end custom radiobutton (circle)*/
  
